.sidebar {

  &-floating {
    position: relative;
    top: -15rem;
    
    @include breakpoint(tablet) {
      top: 0;
    }

    > figcaption {
      padding-top: 2.25rem;
      text-align: center;

      > p {
        color: $primary-grey;
        font-family: $font-secondary;
        font-size: 1.125rem;
      }

      > ul {
        display: inline-flex;
        margin-top: 1rem;
        
        > li {
          align-items: center;
          display: flex;
          justify-content: center;
          margin: 0.625rem;
          padding: 0.25rem;
        }
      }
    }

    &.float-in-xs {
      .card {
        position: absolute;
        height: 15rem;
        min-height: auto;
        top: -9rem;

        > section {
          > ul {
            height: auto;
          }
        }
      }
    }
  }

  > .card {
    cursor: auto;

    > section {
      border-top: 0;
      height: 21.875rem;
      padding: 1.25rem;

      > ul {
        height: 100%;
        border-bottom: 0;
        padding-top: 1.25rem;

        li {
          margin-bottom: 1rem;

          > p {
            font-size: 1rem;
          }
        }
      }
    }
  }
}