.button {
  display: inline-block;
  padding: 0.75rem 1.8935rem;
  color: $white;
  text-align: center;
  outline: none;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  z-index: 1;
  @include transition(all .15s ease-in-out);

  &:hover,
  &:focus {
    color: $white;
    text-decoration: none;
  }
  
  &--small {
    font-size: 14px;
    @include font-weight(strong);
  }
  
  &--block { 
    width: 100%;
  }
  
  &--primary {
    background: linear-gradient(107deg, #006bb9, #005898 49%, #0e418a 100%);
  }

  &--custom {
    background: $primary-blue;

    &:hover,
    &:focus {
      opacity: .85;
    }
  }

  &--select {
    background-color: transparent;
    border: 1px solid #c1c1c1;
    font-family: $font-secondary;
    font-size: 1rem;
    padding: 1rem 0.875rem;
  }

  &--transparent {
    background: transparent;
    color: $primary-gold;
    display: inline-block;
    font-size: 1rem;
    font-weight: 500;
    max-width: 8rem;
    padding: 0.25rem 0;

    &:hover,
    &:focus {
      color: $primary-gold;
      opacity: .95;
    }
  }

  &--secondary {
    background-color: $primary-gold;
    font-size: 1.0625rem;

    &:hover,
    &:focus {
      opacity: .85;
    }
  }

  &--outline {
    border: 1px solid #c1c1c1;
    color: $primary-grey;
    font-size: 1.0625rem;
    line-height: 1.35;
    
    &:hover,
    &:focus {
      opacity: .85;
      color: $primary-grey;
    }
  }

  &--outline-prime {
    border: 1px solid $primary-gold;
    color: $primary-gold;
    font-size: 1.0625rem;
    line-height: 1.35;
    
    &:hover,
    &:focus {
      opacity: .85;
      background-color: $primary-gold;
      color: $white;
    }
  }
}

.button-group {
  margin-bottom: 4rem;
  @include clearfix;
}

#buttonClose {
  display: none;
}

@include breakpoint(phablet) {
  #buttonClose {
    align-items: center;
    color: $white;
    display: flex;
    font-size: 1.5rem;
    font-weight: 100;
    justify-content: center;
    height: 3rem;
    padding: 1rem;
    position: absolute;
    right: 1rem;
    top: 1.25rem;
    width: 3rem;
    z-index: 2;
    @include transition(ease-in-out 0.15s all);

    &:hover {
      @include breakpoint(phablet) {
        background-color: rgba(0, 0, 0, 0.15);
        border-radius: 5rem;
      }
    }
  }
}

button[disabled],
input[disabled] {
  background-color: #b1b1b1;
  color: $white;
  
  &:hover,
  &:focus {
    color: $white;
    opacity: 1;
  }
}