main.container {
  min-height: 40rem;
  padding-bottom: 1.25rem;
  padding-top: 5rem;
  > .row {
    > .container {
      > h2 {
        font-size: 2rem;
        font-weight: 600;
      }
    }
  }

  section.content-primary {
    margin-top: 3rem;
    position: relative;

    .title-container {
      align-items: center;
      display: flex;
      margin-left: 1rem;
      margin-right: 1rem;
      margin-bottom: 2.5rem;
      position: relative;

      @include breakpoint(mobile-large) {
        align-items: flex-start;
        height: 6rem;
      }

      > p {
        font-family: $font-secondary;
        font-size: 1.25rem;
        max-width: 100%;
        width: 25rem;

        @include breakpoint(tablet) {
          font-size: 1rem;
          width: 15rem;
        }
      }

      > a[role="button"] {
        position: absolute;
        right: 0;

        @include breakpoint(tablet) {
          font-size: 1rem;
          padding: 0.5rem;
        }

        @include breakpoint(mobile-large) {
          bottom: 0;
          left: 0;
        }
      }
    }
  }
}

main.container-background {
  margin-top: 3.75rem;
  position: relative;
  
  &:before {
    content: "";
    background: url("../../assets/images/image-mainevent@2x.png") no-repeat center;
    background-size: cover;
    height: 30rem;
    margin: 0 -1rem;
    position: absolute;
    width: 100%;
    z-index: -2;
  }

  &:after {
    content: "";
    background: linear-gradient(to top, rgba(0, 0, 0, 0.7), rgba(44, 44, 44, 0.15));
    background-size: cover;
    height: 30rem;
    margin: 0 -1rem;
    position: absolute;
    width: 100%;
    z-index: -1;
    top: 0;
  }

  > .row {
    > .container {
      padding-top: 7.75rem;

      > .row {
        >.content-primary {
          .title-container {
            align-items: center;
            display: flex;
            margin-left: 1rem;
            margin-right: 1rem;
            margin-bottom: 3rem;
            position: relative;

            @include breakpoint(mobile-large) {
              align-items: flex-start;
              height: 6rem;
            }
  
            > h1 {
              color: $white;
              font-size: 3rem;
              font-weight: 600;

              @include breakpoint(tablet) {
                font-size: 1.875rem;
                width: 15rem;
              }
            }
  
            > a[role="button"] {
              position: absolute;
              right: 0;

              @include breakpoint(tablet) {
                font-size: 1rem;
                padding: 0.5rem;
              }

              @include breakpoint(mobile-large) {
                bottom: 0;
                left: 0;
              }
            }
          }
        }
        > .container {
          display: flex;
          flex-direction: column;
          
          > img {
            max-width: 7.5625rem;
            margin-bottom: 1.25rem;
          }
    
          > h2 {
            color: $white;
            font-weight: 400;
            margin-bottom: 1rem;
          }
        }
      }
    }
  }
}