.navbar-container {
  align-items: center;
  background-color: transparent;
  display: flex;
  margin: 0 auto;
  max-width: 73.125rem;
  padding-bottom: 0.875rem;
  padding-top: 0.875rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  width: 100%;

  .navbar-header {
    display: flex;
    flex-wrap: wrap;
    position: relative;

    @include breakpoint(phablet) {
      width: 100%;
    }
  }

  .navbar-nav {
    margin-left: 1rem;
    > li {
      > a {
        align-items: center;
        color: $white;
        font-size: 1.125rem;
        display: flex;
        flex-direction: row;
        line-height: 1.5rem;
        justify-content: center;
        outline: none;
        @include transition(ease-in 0.15s all);
        @include breakpoint(phablet) {
          line-height: 2.5rem;
        }

        &:hover,
        &:focus,
        &:visited {
          background-color: transparent;

          @include breakpoint(phablet) {
            background-color: rgba(0, 0, 0, 0.15);
            border-radius: 0.25rem;
          }
        }

        > img {
          width: 1.5rem;
          margin-left: 0.5rem;
        }
      }
    }
  }

  .navbar-right {
    @media (min-width: $grid-float-breakpoint) {
      margin-right: 0;
    }
  }
}

@media (min-width: 991px) {
  .navbar-nav>li {
    float: left;
  }
}

.navbar {
  border: none;
  margin-bottom: 0;
  @include transition(ease-in 0.15s all);

  &.is-transparent {
    background-color: transparent;
    border: 0;
    opacity: 1;

    .navbar-logo-wrapper {
      > .navbar-logo-white {
        display: inline-block;
      }
      > .navbar-logo {
        display: none;
      }
      .img {
        &--small {
          width: 4.275rem;
        }
      }
    }

    .navbar-toggle {
      > .menu-white {
        display: block;
      }
      > .menu-dark {
        display: none;
      }
    }
  }

  &.is-fade {
    opacity: 0;

    &.is-shrink {
      background: $white;
      border-bottom: 0.25rem solid $primary-gold;
      opacity: 1;
      @include translate(0, 0);

      .navbar-logo-wrapper {
        > .navbar-logo-white {
          display: none;
        }
        > .navbar-logo {
          display: inline-block;
        }
        .img {
          &--small {
            width: 4.275rem;
          }
        }
      }

      .navbar-toggle {
        > .menu-white {
          display: none;
        }
        > .menu-dark {
          display: block;
        }
      }
      
      .navbar-nav {
        > li > a {
          color: $primary-black;

          @include breakpoint(phablet) {
            color: $white;
          }
        }
      }

      a.btn-auth {
        color: $primary-black;

        &.btn-with-icon {
          background: url("../../assets/images/ico-profile-black.svg") no-repeat right center;
        }
      }
    }
  }
}

.navbar-toggle {
  .icon-bar {
    background-color: $white;
  }
}

.navbar-collapse {
  border-top: none;
  @include box-shadow(none);
}

@include breakpoint(phablet) {
  .navbar-container{
    .navbar-nav {
      margin-top: 10rem;
      margin-left: 0;
  
      li {
        > a { 
          width: 100%;
          text-align: left;
          justify-content: flex-start;
        }
      }
    }
  }
}

// Navbar Drawer
// 
// ----------------------------------
.navbar-drawer {
  display: flex;
  justify-content: space-between;
  min-height: 3.25rem;
  width: 100%;

  .btn-auth {
    align-items: center;
    display: flex;
    font-size: 1.125rem;
    height: 3.375rem;
    justify-content: center;
    padding-left: 0;
    padding-right: 0;
    
    &.btn-with-icon {
      background: url("../../assets/images/ico-profile.svg") no-repeat right center;
      display: inline-block;
      height: 3.2875rem;
      width: 10.5rem;

      > span {
        float: right;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 7rem;
      }

      > span.success {
        float: unset;
      }
    }
  }
}

@include breakpoint(phablet) {
  .navbar-container {
    background-color: inherit;
  }
  .navbar-toggle {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 0;
    padding: 9px;
    position: absolute;
    right: 0;
    top: 50%;
    @include transform(translateY(-50%));
    @include transition(ease-in .15s);

    &:active{
      border-radius: 5rem;
      background: rgba(0,0,0,0.2);
    }
  }
  .navbar-drawer {
    position: fixed;
    height: 100vh;
    top: 0;
    right: 0;
    background: linear-gradient(107deg, #006bb9, #005898 49%, #0e418a 100%);
    padding: 0 24px;
    @include transition(all .2s ease-in-out);
    @include translate(100%, 0);
    
    &.drawer-in {
      width: 100%;
      @include translate(0, 0);
    }
  }
}
.overlay {
  background-color: rgba(0,0,0,0.25);
  overflow: hidden;
}