// $color-text-highlight-success:  $color-green-dark;

// Sidebar
//
// -----------------
$bgcol-sidebar:                 $color-grey-80;
$bgcol-default:                 $color-grey-80;

// Color border
//
// -----------------
$border-grey-light:             $color-grey-20;
$border-grey-strong:            $color-grey-border;

$panel-font-size:               13px;
$panel-line-height:             1.5;
$panel-line-to-show:            2;

// Font
$font-primary: 'Work Sans', 'Helvetica', sans-serif;

:root {
  font-family: $font-primary;
  font-size: 1rem;
  color: $color-grey-900;

  @include breakpoint(full_hd) {
    :root {
      font-size: calc(16 * 100vw / 1920);
    }
  }

  @include breakpoint(hd) {
    :root {
      font-size: calc(16 * 100vw / 1440);
    }
  }

  @include breakpoint(phablet) {
    :root {
      font-size: calc(16 * 100vw / 991);
    }
  }

  @include breakpoint(mobile-medium) {
    :root {
      font-size: calc(16 * 100vw / 360);
    }
  }
}

// Extends panel login
%panel__login{
  padding: 0;
  background: transparent;
  -webkit-box-shadow: none;
  box-shadow: none;
}

// Extends panel forgot
%panel__forgot {
  height: auto;
  min-height: 200px;
  padding-top: 52px;
}

// Extends title form
%form__title {
  font-size: 16px;
  color: $color-grey-900;
  text-align: center;
  margin-top: 0;
  margin-bottom: 4px;
}

// Extends text form
%form__text {
  font-size: 12px;
  font-weight: 100;
  color: $color-spot-dark-blue;
  text-align: center;
  margin: 0 0 32px;
}

// Extends success state
%success {
  font-size: 12px;
  color: $alert-success;
  text-align: center;
  margin-top: 0;
  margin-bottom: 12px;
}