.area-background {
  display: block;
  width: 100%;
  height: 100vh;
  position: relative;

  > figure {
    position: absolute;
    bottom: 12%;
    left: 50%;
    width: 19.6875rem;
    @include transform(translateX(-57%));

    > img {
      display: inline-block;
      width: 6.25rem;
    }

    > h1,
    > p {
      color: $white;
      font-size: 2rem;
    }

    > h1 {
      margin-top: 2rem;
    }
  }
}

.area-form {
  display: flex;
  position: relative;
  height: 100%;

  .container-form {
    display: flex;
    flex-direction: column;
    margin: auto;
    max-width: 39.6875rem;
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 5rem;

    @include breakpoint(mobile-large) {
      margin-bottom: 3.5rem;
    }

    > a[role="button"] {
      position: absolute;
      right: -0.875rem;
      top: 2rem;
      padding: 0.5rem;
      @include transition(all .25s ease-in-out);
  
      &:hover {
        background: rgba(0, 0, 0, 0.15);
        border-radius: 5rem;
      }
    }

    .container-pre-form {
      margin-bottom: 1.25rem;

      > h2 {
        font-size: 2rem;
        font-weight: 600;
        margin-bottom: 1rem;
      }

      > p {
        color: $primary-grey;
        font-family: $font-secondary;
        font-size: 1.125rem;
        margin-bottom: 0.5rem;
      }
    }

    > span {
      color: $primary-grey;
      font-family: $font-secondary;
      font-size: 1.125rem;
      margin-bottom: 0.5rem;
      position: relative;
      bottom: 2.5rem;

      @include breakpoint(mobile-large) {
        bottom: 6rem;
      }
    }

    form {
      .form-group-submission {
        position: relative;
        bottom: -4.5rem;
        align-items: center;
        display: flex;
        justify-content: space-between;

        @include breakpoint(mobile-large) {
         bottom: -7.5rem;
         flex-direction: column-reverse;

         > button {
           margin-bottom: 1rem;
           width: 100%;
         }
        }

        > span {
          color: $primary-grey;
          font-family: $font-secondary;
          font-size: 1.125rem;
          margin-bottom: 0.5rem;

          @include breakpoint(mobile-large) {
            // margin-bottom: 8rem;
           }
        }
      }
    }
  }
}