.card-profile {
  cursor: inherit;

  &-custom {
    min-height: auto;
    height: 23.65rem;
    margin-bottom: 5rem;

    @include breakpoint(phablet) {
      height: auto;
    }

    > img {
      min-height: 6.25rem;
    }

    > section.experience {
      height: 100%;

      > h2 {
        font-size: 2rem;
        font-weight: 600;
        margin-bottom: 1rem;
      }

      > h3 {
        font-size: 1.25rem;
        font-weight: 600;
        margin-bottom: 1.5rem;
        margin-top: 0.875rem;
      }
      
      > h5 {
        font-size: 1rem;
        font-weight: 600;
        margin-bottom: 1rem;
      }

      > span {
        color: $primary-grey;
        font-family: $font-secondary;
        font-size: 1.25rem;
        min-height: auto;
        height: 100%;
      }

      > p {
        color: $primary-grey;
        font-family: $font-secondary;
        font-size: 1rem;
        min-height: auto;
        height: 100%;
      }

      > a.button--outline-prime {
        align-self: flex-end;
        color: $primary-gold;
        display: flex;
        justify-content: center;
        max-width: 100%;
        width: 13.1875rem;
  
        &:hover,
        &:focus {
          color: $white;
        }
      }
    }
  }

  > section.profile {
    border-top: 0;
    margin-top: 1rem;
    padding: 0;

    > p {
      color: $primary-grey;
      min-height: auto;
    }

    > a.button--outline-prime {
      color: $primary-gold;
      display: flex;
      justify-content: center;
      width: 100%;

      &:hover,
      &:focus {
        color: $white;
      }
    }
  }
}

.card-footer-extra {
  margin-bottom: 4rem;
}