.detail {
  position: relative;

  @include breakpoint(tablet) {
   margin-top: 8rem;
  }

  > .detail-content {
    margin-bottom: 2.5rem;
    max-width: 100%;
    width: 39.6875rem;

    @include breakpoint(tablet) {
      width: 100%;
    }

    > .detail-location {
      border-radius: 2rem;
      overflow: hidden;
      max-width: 100%;
      width: 39.6875rem;
    }

    > h2 {
      font-size: 2rem;
      font-weight: 600;
      margin-bottom: 1.25rem;
    }
    > p {
      color: $primary-grey;
      font-size: 1.125rem;
      font-family: $font-secondary;
      margin-bottom: 1.25rem;
    }
    > figure {
      margin-top: 1.25rem;
      margin-bottom: 1.25rem;

      > img {
        margin-bottom: 1.25rem;
        width: 100%;
      }

      > span {
        color: $primary-grey;
        display: flex;
        justify-content: center;
        font-family: $font-secondary;
      }
    }
  }
}