.card {
  background: $white;
  border-radius: 0.75rem; 
  box-shadow: 0 3px 5px -1px rgba(117, 127, 136, 0.2), 0 1px 18px 0 rgba(117, 127, 136, 0.12), 0 6px 10px 0 rgba(117, 127, 136, 0.14);
  color: $primary-black;
  display: flex;
  flex-direction: column;
  margin-bottom: 1.875rem;
  min-height: 21.875rem;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  width: 100%;
  cursor: pointer;

  &:hover {
    color: $primary-black;
    text-decoration: none;
  }

  > figcaption {
    background: $primary-gold;
    border-radius: 5rem;
    color: #fff;
    font-family: $font-primary;
    font-size: 0.75rem;
    padding: 0.125rem 0.75rem;
    position: absolute;
    right: 1rem;
    top: 1rem;
  }
  
  > section {
    border-top: 0.25rem solid $primary-gold;
    display: flex;
    flex-direction: column;
    padding: 1.25rem 1.25rem 0.875rem;

    > button {
      align-self: flex-end;
    }

    > span {
      margin-bottom: 1rem;
    }

    > h4 {
      font-size: 1.25rem;
      font-weight: 600;
      margin-bottom: 0.75rem;
    }

    > p {
      font-size: 1rem;
      font-family: $font-secondary;
      margin-bottom: 1rem;
    }

    > ul {
      border-bottom: 0.0625rem solid rgba(117, 127, 136, 0.14);
      margin-bottom: 1rem;
      padding-bottom: 0.25rem;
      padding-left: 0;

      li {
        align-items: center;
        display: flex;
        margin-bottom: 0.5rem;

        > img {
          margin-right: 0.5rem;
        }
      }
    }
  }

  &.card-image {
    box-shadow: none;
    margin-bottom: 1rem;
    margin-top: 1rem;

    @include breakpoint(tablet) {
      min-height: auto;
    }

    > a {
      position: relative;
      overflow: hidden;
      text-decoration: none;
      @include transition(all .25s ease-in-out);

      > img {
        width: 100%;
        height: 100%;
      }

      > p {
        display: none;
      }
      
      &:hover {
        @include transition(all .25s ease-in-out);

        &:before {
          background: linear-gradient(to bottom, rgba(48, 48, 48, 0.8), rgba(0, 0, 0, 0.8));
          content: "";
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
        }

        > p {
          position: absolute;
          color: $white;
          display: flex;
          font-size: 1rem;
          margin: 0 4rem;
          text-align: center;
          top: 50%;
          transform: translateY(-50%);
          @include transition(all .25s ease-in-out);
        }
      }
    }
  }
}

.card-profile {
  padding: 1.25rem;

  > img {
    border-radius: 10rem;
    overflow: hidden;
    width: 6.25rem;
  }

  > section {
    border-top: 0;
    margin-top: 1rem;
    padding: 0;

    > p {
      min-height: 6.2875rem;
    }

    > a {
      align-items: center;
      color: $primary-black;
      display: flex;

      img {
        margin-right: 0.5rem;
        width: 1rem;
      }
    }
  }
}

.card-event-blank {
  cursor: inherit;
  min-height: auto;
  height: 25.75rem;
  margin-top: 2rem;

  > section {
    align-items: center;
    border-top: none;
    height: 100%;
    justify-content: center;
    padding: 1.25rem;

    > h1 {
      font-size: 2rem;
      font-weight: 600;
      margin-bottom: 1.25rem;
      text-align: center;
    }

    > p {
      font-size: 1.25rem;
      margin-bottom: 4rem;
      max-width: 35rem;
      text-align: center;
    }
  }
}

.card-footer {
  display: flex;
  justify-content: center;
  width: 100%;

  > a[role="button"] {
    margin: 1rem 0;
  }
}