footer {
  background: linear-gradient(107deg, #006bb9, #005898 49%, #0e418a 100%);
  margin-top: 5.5rem;

  > .footer-sitemap {
    display: flex;
    justify-content: space-between;
    margin: auto;
    max-width: 75rem;
    padding-bottom: 5.125rem;
    padding-top: 3.125rem;
    width: 100%;

    .about {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      max-width: 41rem;
      
      @include breakpoint(phablet) {
        max-width: 100%;
        width: 100%;
        margin-bottom: 3rem;
      }

      > img {
        margin-bottom: 1.25rem;
        width: 5rem;
      }

      > h3 {
        margin-bottom: 1.25rem;
      }

      > span > p {
        font-size: 1rem;
        font-weight: 300;
      }

      h2, h3, span > p {
        color: $white;
      }
    }

    .sitemap {
      display: flex;
      justify-content: flex-end;

      @include breakpoint(phablet) {
        display: inherit;
        justify-content: inherit;
      }

      ul {
        margin-left: 5rem;
        min-width: 7rem;

        @include breakpoint(phablet) {
          margin-left: 0;
          margin-bottom: 2rem;
        }
        
        > h6 {
          color: $white;
          font-size: 1.25rem;
          margin-bottom: 1.25rem;
        }

        li {
          margin-bottom: 0.5rem;

          > a {
            color: $light-white;
            font-size: 1rem;
            font-weight: 300;
          }
        }
      }
    }
  }

  .footer-copy {
    align-items: center;
    background-color: $darker-blue;
    color: $light-white;
    display: flex;
    font-family: $font-secondary;
    font-weight: 300;
    min-height: 3.5rem;
    justify-content: center;
    padding: 1rem;
    text-align: center;
  }
}