main.container-plain {
  > .container {
    > .row {
      h1 {
        display: flex;
        align-items: center;
        position: relative;
        font-size: 2rem;
        font-weight: 600;
        > span {
          border-radius: 4rem;
          border: 1px solid;
          display: inline-flex;
          width: 2rem;
          height: 2rem;
          justify-content: center;
          align-items: center;
          font-size: 1.125rem;
          position: absolute;
          left: -5rem;
        }
      }
      h2 {
        display: flex;
        align-items: flex-start;
        position: relative;
        font-size: 1.25rem;
        font-weight: 600;
        > span {
          border-radius: 4rem;
          border: 1px solid;
          display: inline-flex;
          width: 2rem;
          height: 2rem;
          justify-content: center;
          align-items: center;
          font-size: 1.125rem;
          position: absolute;
          left: -5rem;
        }
      }

      section {
        > .container-form {
          margin-top:3.5rem;
        }
      }
    }
  }
}