fieldset {
  input[type="checkbox"] {
    display: none;
  }

  > label {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 3.5rem;
    position: relative;
    cursor: pointer;
  }

  label > i {
    position: relative;
    width: 1rem;
    height: 1rem;
    margin-right: 0.875rem;
  }

  label > i:before {
    content: "";
    display: block;
    border: 1px solid #c1c1c1;
    border-radius: 5rem;
    width: 1rem;
    height: 1rem;
    @include transition(all 0.2s ease-in-out);
  }

  label > i:after {
    content: "";
    display: block;
    border-radius: 5rem;
    width: 0.5rem;
    height: 0.5rem;
    position: absolute;
    left: 1.875rem;
    background-color: $primary-gold;
    position: absolute;
    top: 4px;
    left: 4px;
    opacity: 0;
    @include transition(ease-in-out 0.2s all);
  }

  label > input[type="checkbox"]:not(:checked) + i:after {
    opacity: 1;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  
  label > input[type="checkbox"]:checked + i:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

fieldset.survey-checkbox {
  border: 2px solid $primary-black !important;

  &.blocking {
    max-width: 12.5rem !important;
    width: 100% !important;
  }

  &.with-cross {
    label > i:before {
      background: url('../../assets/images/ico-cb-cross-invalid.svg') no-repeat center;
    }
  
    label > i:after {
      background: url('../../assets/images/ico-cb-cross-valid.svg') no-repeat center;
    }
  }

  label > i:before {
    background: url('../../assets/images/ico-cb-invalid.svg') no-repeat center;
    border: none;
    border-radius: 4px;
    display: inline-block;
    width: 1.25rem;
    height: 1.25rem;
  }

  label > i:after {
    background: url('../../assets/images/ico-cb-valid.svg') no-repeat center;
    border-radius: 0;
    display: inline-block;
    width: 1.25rem;
    height: 1.25rem;
    top: 0;
    left: 0;
  }

  label > input[type="checkbox"]:not(:checked) ~ span {
    color: #c1c1c1;
  }

  label > input[type="checkbox"]:checked ~ div.bg {
    position: absolute;
    left: 0;
    width: 100%;
    height: 56px;
    background: #ececec;
    z-index: -1;
  }
}