.modal-dialog {
  min-height: calc(100% - (1.75rem * 2));

  &.modal-dialog-centered {
    display: flex;
    align-items: center;
  }

  &.modal-submit {
    max-width: 31.25rem;
  }

  .modal-content {
    box-shadow: none;
    border-radius: 1rem;
  }

  .modal-title {
    font-size: 1.75rem;
    font-weight: 600;
  }

  .modal-header {
    border-bottom: none;
    padding: 2.5rem 2.5rem 0;
  }

  .modal-body {
    padding: 1rem 2.5rem 1.75rem;

    p {
      font-size: 1.125rem;
    }
  }
  
  .modal-footer {
    border-top: none;
    padding: 0 2.5rem 2.5rem;

    .button--outline-prime {
      background: $white;

      &:hover,
      &:focus {
        background: $white;
        color: $primary-gold;
        opacity: 0.875;
      }
    }
  }
}