main.container-plain {
  > .container {
    section.content-policy {
      article {
        ol {
          list-style-type: upper-latin;
          padding-left: 1.25rem;
          margin-bottom: 1rem;
          
          li {
            font-family: $font-secondary;
            font-size: 1.25rem;
            font-weight: 600;
            margin-bottom: 1rem;

            h5 {
              font-family: $font-secondary;
              font-weight: 600;
              font-size: 1.25rem;
              margin-bottom: 1rem;
            }
      
            > ul {
              list-style-type: disc;
              padding-left: 2rem;
      
              li {
                margin-bottom: 0.5rem;
    
                p {
                  color: $primary-grey;
                  font-size: 1rem;
                  font-weight: normal;
                  line-height: 1.56;
                  margin-bottom: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}
