.pagination-custom {
  display: inline-flex;
  justify-content: center;
  margin: 2.5rem 0;
  width: 100%;

  > li {
    padding: 0.25rem;
    
    > a,
    > span {
      border-radius: 0.25rem;
      border: 1px solid #c1c1c1;
      color: $primary-black;
      padding: 0.5625rem 0.75rem;
      text-align: center;
      width: 2.5rem;
    }

    > a:hover,
    > a:focus,
    > span:hover,
    > span:focus {
      border-color: #c1c1c1;
      color: $primary-grey;
    }
  }

  > .active > a,
  > .active > a:hover,
  > .active > a:focus,
  > .active > span,
  > .active > span:hover,
  > .active > span:focus {
    background-color: $primary-gold;
    border-color: inherit;
  }
}