.container-form {
  position: relative;

  form {
    .form-group {
      display: flex;
      flex-direction: column;
      margin-bottom: 1rem;

      fieldset {
        border: 1px solid #c1c1c1;
        border-radius: 0.5rem;
        max-width: 100%;
        overflow: hidden;
        width: 10.0625rem;
      }

      label {
        display: flex;
        font-size: 1.125rem;
        font-weight: 500;
      }

      > input {
        border: 1px solid #c1c1c1;
        border-radius: 0.25rem;
        font-size: 1rem;
        min-height: 3.5rem;
      }

      > textarea {
        padding: 1rem 0.75rem;
        height: 8rem;
        resize: none;
        font-size: 1rem;
        font-family: $font-secondary;
      }

      .form-control {
        &.input-invalid {
          border-color: $alert-danger;
        }
      }

      .form-control:focus{
        box-shadow: none;
        border-color: $primary-gold;
      }

      input::placeholder {
        font-family: $font-secondary;
      }

      .invalid {
        color: $alert-danger;
        font-size: 0.75rem;
        font-family: $font-secondary;
        font-style: italic;
        min-height: 1rem;
        padding-top: 0.125rem;
      }
    }
  }
}

.custom-file {  
  border: 1px solid #c1c1c1;
  border-radius: 0.25rem;
  height: 3.5rem;
  width: 100%;

  input[type="file"] {
    display: none;
  }

  .custom-file-label {
    cursor: pointer;
    display: flex;
    height: 100%;
    padding: 1rem;

    span {
      align-self: center;
      font-size: 1rem;
      font-family: $font-secondary;
      color: #999999;
      margin-bottom: 0;
    }
  }
}

.row {
  &.row-submit {
    margin-bottom: 3.5rem;
    margin-top: 3rem;
  }
}