fieldset {
  input[type="radio"] {
    display: none;
  }

  > label {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 3.5rem;
    position: relative;
    cursor: pointer;
  }

  label > i {
    position: relative;
    width: 1rem;
    height: 1rem;
    margin-right: 0.875rem;
  }

  label > i:before {
    content: "";
    display: block;
    border: 1px solid #c1c1c1;
    border-radius: 5rem;
    width: 1rem;
    height: 1rem;
    @include transition(all 0.2s ease-in-out);
  }

  label > i:after {
    content: "";
    display: block;
    border-radius: 5rem;
    width: 0.5rem;
    height: 0.5rem;
    position: absolute;
    left: 1.875rem;
    background-color: $primary-gold;
    position: absolute;
    top: 4px;
    left: 4px;
    opacity: 0;
    @include transition(ease-in-out 0.2s all);
  }

  label > input[type="radio"]:not(:checked) + i:after {
    opacity: 1;
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  label > input[type="radio"]:checked + i:after{
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

fieldset.survey {
  border: 2px solid $primary-black !important;

  label > i:before {
    border: 2px solid $primary-black;
  }

  label > i:after {
    content: "";
    background-color: $primary-black;
  }

  label > input[type="radio"]:not(:checked) ~ span {
    color: #c1c1c1;
  }

  label > input[type="radio"]:checked ~ div.bg {
    position: absolute;
    left: 0;
    width: 100%;
    height: 56px;
    background: #ececec;
    z-index: -1;
  }
}

fieldset.survey-rate {
  display: inline-block;
  border: 2px solid $primary-black !important;
  width: 60px !important;

  label > i {
    display: none;
  }

  label > input[type="radio"]:checked ~ div.bg {
    position: absolute;
    left: 0;
    width: 100%;
    height: 56px;
    background: #ececec;
    z-index: -1;
  }
}

fieldset.cart-option {
  margin-bottom: 1.5rem;

  > label {
    border-radius: 0.75rem;
    box-shadow: 0 3px 5px -1px rgba(117, 127, 136, 0.2), 0 1px 18px 0 rgba(117, 127, 136, 0.12), 0 6px 10px 0 rgba(117, 127, 136, 0.14);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 0;
    height: 100%;
    padding: 2rem 1.5rem;
    position: relative;
    cursor: pointer;

    span {
      margin-right: 1.5rem;
      width: 100%;

      h6 {
        font-size: 1.25rem;
        font-weight: 600;
        margin-bottom: 0.25rem;
      }

      p {
        color: $primary-grey;
        font-size: 1.125rem;
        font-weight: normal;
        font-family: $font-secondary;
      }
    }

    i {
      height: 1.5rem;
      width: 1.5rem;
    }
    
    i:before {
      content: "";
      height: 1.5rem;
      width: 1.5rem;
    }

    i:after {
      content: "";
      height: 0.75rem;
      width: 0.75rem;
      left: 6px;
      top: 6px;
    }
  }
}