@import "../themes/_palette.scss";

body {
  color: $primary-black;
  font-family: $font-primary;
}

ul,
ol {
  list-style: none;
}

ul { padding-left: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

span {
  display: block;
}

span.misc {
  color: #555;
  display: flex;
  flex-direction: row;
  font-family: $font-secondary;
  font-size: 1.125rem;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  margin-bottom: 0.5rem;

  > p {
    margin-bottom: 0 !important;
  }
}

p {
  margin: 0;
}

a {
  color: $secondary-blue;
}

.text__state {
  &--success {
    font-size: 11px;
    @include font-weight(strong);
    color: $alert-success;
  }  
  &--alert {
    font-size: 11px;
    @include font-weight(strong);
    color: $alert-danger;
  }
  &--disable {
    color: $color-grey-border;
    @include font-weight(light);
  }
}

.text--strong {
  color: $color-grey-900;
  @include font-weight(strong);
}
.text--warning {
  color: $alert-danger;
}
.text--capital {
  text-transform: uppercase;
}
.is-hide {
  display: none;
}
.is-hide-visible {
  visibility: hidden;
}

// Modal styling
.spot-modal__content {
  background: $color-grey-20;

  .spot-label__container {
    @extend .spot-label__container;
    > p {
      @include font-size(small);
    }
  }  
  .modal-header {
    padding: 24px 24px 32px;
  }
  .spot-modal__body {
    padding: 0 24px;
  }
  .modal-footer {
    padding: 0 24px 24px;
  }
}

.flex-wrap {
  display: flex;
  flex-direction: column;
  margin-left: 24px;
  
  @include breakpoint(phablet) {
    margin-bottom: 18px;
  }

   &--centered {
    justify-content: center;
  }

}

.flex-wrap-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 24px;

  .flex-wrap-item {
    display: inline-flex;
    align-items: flex-end;
  }
}