// Jumbotron
// --------------------------------------------------

.jumbotron {
  color: $jumbotron-color;
  margin-bottom: 0;
  padding-bottom: 0;
  padding-top: 0;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(to top, rgba(0,0,0,0.7), rgba(44,44,44,0.15));
    z-index: 0;
  }
  
  .jumbotron__wrapper {
    display: block;
    width: 100%;

    .container {
      align-items: center;
      display: flex;
      height: 100%;
      min-height: 37.5rem;

      @include breakpoint(tablet) {
        min-height: auto;
        height: 28rem;
      }

      .hero-text {
        max-width: 34.6875rem;
        z-index: 1;

        @include breakpoint(tablet) {
          max-width: 100%;
          margin-right: 1.5rem;
        }
        
        h2 {
          color: $white;
          font-size: 3rem;
          margin-bottom: 1.5rem;

          @include breakpoint(tablet) {
            font-size: 2rem;
          }
        }

        p {
          color: $white;
          font-family: $font-secondary;
          font-size: 1.25rem;
          font-weight: 300;
          max-width: 28rem;
          @include breakpoint(tablet) {
            max-width: 100%;
          }
        }
      }
      
    }

    &--main {
      height: auto;
      min-height: 37.5rem;

      @include breakpoint(tablet) {
        height: 28rem;
        min-height: auto;
      }
    }

    &.jumbotron-event {
      @include breakpoint(tablet) {
        height: 22rem;
        min-height: auto;
      }

      .container {
        .hero-text {
          position: absolute;
          bottom: 5rem;
        }
      }
    }
    
    &--secondary {
      min-height: 29.6875rem;
    }
  }

  @media screen and (min-width: $screen-sm-min) {
    padding-top:    0;
    padding-bottom: 0;

    .container &,
    .container-fluid & {
      padding-left:  ($jumbotron-padding * 2);
      padding-right: ($jumbotron-padding * 2);
    }

    h1,
    .h1 {
      font-size: $jumbotron-heading-font-size;
    }
  }
}
