.dropdown-menu {
  padding-bottom: 0;
  padding-top: 0;

  > li > a {
    font-family: $font-secondary;
    font-size: 0.875rem;
    padding: 0.875rem 1.25rem;

    &:focus,
    &:active {
      outline: none;
      background-color: #f0f0f0;
    } 
  }

  > .active > a,
  > .active > a:hover,
  > .active > a:focus {
    background: $primary-blue;
  }
}

.navbar-nav > li {
  >.dropdown-nav {
    border: none;
    border-radius: 1rem;
    border-top-right-radius: 1rem;
    border-top-left-radius: 1rem;
    box-shadow: 0 -1px 4px 0 rgba(0, 0, 0, 0.04), 0 4px 8px 0 rgba(0, 0, 0, 0.08);
    overflow: hidden;
    width: 14rem;

    li > a {
      font-size: 1rem;
    }

    li.important > a {
      color: #ff0000;
    }

    .divider {
      margin: 0;
    }
  }
}

@include breakpoint(phablet) {
  .navbar-nav .open .dropdown-nav {
    border-top-right-radius: 0.25rem;
    border-top-left-radius: 0.25rem;  
    border-bottom-right-radius: 0.25rem;
    border-bottom-left-radius: 0.25rem;  
    padding-top: 0.5rem;
  }

  .navbar-nav .open .dropdown-nav>li>a {
    color: $white;
    font-size: 1.25rem;
    padding: 1rem 1.5rem;
  
    &:hover,
    &:focus {
      background-color:rgba(0, 0, 0, 0.15);    
      border-radius: 0.25rem;
    }
  }
}
