.content-checkout {
  margin-bottom: 3rem;

  form {
    max-width: 100%;
    width: 41rem;

    button.button-checkout {
      margin-bottom: 5rem;
    }

    @include breakpoint(phablet) {
      width: 100%;
    }
  }
}

.title-event {
  h2 {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
  p {
    color: $primary-grey;
    font-family: $font-secondary;
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
}