main.container-plain {
  padding-top: 10rem;

  > .container {
    max-width: 100%;
    width: 45.625rem;

    h1.title-strong {
      font-size: 3rem;
      font-weight: 600;
      margin-bottom: 1rem;
    }

    p {
      color: $primary-grey;
      font-size: 1.125rem;
      font-family: $font-secondary;
      margin-bottom: 2.25rem;
    }

    section.content {
      article {
        margin-bottom: 2.5rem;

        h2 {
          font-size: 2rem;
          font-weight: 600;
          margin-bottom: 1.25rem;
        }

        p {
          color: $primary-grey;
          font-family: $font-secondary;
          font-size: 1.125rem;
          line-height: 1.75rem;
          margin-bottom: 1rem;
        }
      }
    }
  }
}