.container-form {
  .contact {
    .form-group {
      margin-bottom: 1.5rem;
    }
    button[type="submit"] {
      margin-bottom: 1.5rem;
    }
  }
}

section.wrapper-foreground {
  position: relative;
  margin-top: 5rem;
  padding-left: 103px;

  @include breakpoint(phablet) {
    margin-top: 10rem;
    padding-left: 0;
  }

  img {
    width: 100%;
    max-width: 21.375rem;
  }

  .back {
    position: absolute;
    top: -103px;
    left: 206px;
    z-index: -1;

    @include breakpoint(phablet) { 
      left: 103px;
    }
  }
}