.jumbotron {
  .jumbotron__wrapper {
    .container {
      .hero-text-article {
        max-width: 40rem;
        bottom: 3.125rem;
        position: absolute;
        p { max-width: 100%; }
        @include breakpoint(tablet) {
          padding-top: 20%;
        }
      }
    }
  }
}

.detail-article {
  margin: auto;
  max-width: 100%;
  padding-bottom: 5rem;
  width: 45.625rem;

  p {
    color: $primary-grey;
    font-size: 1.125rem;
    font-family: $font-secondary;
    margin-bottom: 1.25rem;
  }

  figure {
    img {
      width: 100%;
    }
    > span {
      color: $primary-grey;
      display: flex;
      justify-content: center;
      font-family: $font-secondary;
      margin: 1rem 0;
    }
  }

  .title-article {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.25rem;

    h5,
    h6 > p {
      color: $primary-black;
      font-family: $font-secondary;
      font-size: 1.25rem;
      font-weight: normal;
      line-height: 1.8;
    }

    h6 {
      align-items: center;
      display: flex;
      justify-content: space-between;

      p { margin-bottom: 0; }
      ul {
        align-items: center;
        display: inline-flex;
        margin-bottom: 0;
        padding: 0;

        li {
          margin-left: 1rem;
          padding: 0.125rem;
          width: 1.75rem;
        }
      }
    }
  }
}

@include breakpoint(tablet) {
  .detail-article {
    margin-top: -8rem;
  }
}