main.container section.content-404 {
  margin-bottom: 5rem;
  position: relative;

  @include breakpoint(phablet) {
    margin-top: 0;
  }
  
  .title {
    h2 {
      font-size: 2rem;
      font-weight: 600;
      margin-bottom: 1rem;
    }
    p {
      color: $primary-grey;
      font-family: $font-secondary;
      font-size: 1.25rem;
      margin-bottom: 1.5rem;
    }
  }
}

section.wrapper-foreground {
  position: relative;
  margin-top: 5rem;
  padding-left: 103px;

  @include breakpoint(phablet) {
    margin-top: 10rem;
    padding-left: 0;
  }

  img {
    width: 100%;
    max-width: 21.375rem;
  }

  .back {
    position: absolute;
    top: -103px;
    left: 206px;
    z-index: -1;
    @include breakpoint(phablet) { 
      left: 103px;
    }
  }
}