.container-form {
  form {
    > .form-survey {
      margin-bottom: 2.5rem;

      .label-survey {
        position: relative;
        margin-bottom: 1.5rem;
    
        > span {
          border-radius: 4rem;
          border: 1px solid;
          font-weight: 600;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          font-size: 1.125rem;
          position: absolute;
          left: -5rem;
          height: 2rem;
          width: 2rem;
        }
    
        > h2 {
          display: flex;
          align-items: flex-start;
          font-size: 1.25rem;
          font-weight: 600;
        }
      }
  
      input.form-control {
        padding-left: 1rem;
        padding-right: 1rem;
      }
      
      > textarea {
        height: 12rem;
        padding-left: 1rem;
        padding-right: 1rem;
      }

      fieldset {
        margin-bottom: 1rem;
        
        label {
          display: flex;
          justify-content: flex-start;
          padding: 0.5rem 1.5rem;
        }
      }
    }
  }
}